export default {
	data() {
		return {
			// 服务器地址
			baseUrl: getApp().globalData.baseUrl,
			// 上传作品信息相关
			description: '',
			duration: 0,
			tempCoverUrl: '',
			tempHeight: '',
			tempWidth: '',
			tempVideoUrl: '',
			// BGM相关
			bgmId: '',
			bgmList: [],
			bgmName: '',
			bgmAuthor: '',
			bgmSrc: '',
			bgmCover: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570011702869&di=bb184e38fd80df41fa12a53831e22bf1&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201804%2F25%2F20180425214237_fnkuj.thumb.700_0.jpg',
			// 状态控制
			isSelectVideo: false,
			isSetCoverImage: false,
			isSelectBgm: false,
			isShowSelectModel: false,
			isUpload: false,
			isLoading: false,
			// 步骤条
			basics: 0,
			basicsList: [{
					cuIcon: 'video',
					name: '选择视频'
				},
				{
					cuIcon: 'musicfill',
					name: '选择背景音乐'
				},
				{
					cuIcon: 'roundcheckfill',
					name: '上传成功'
				}
			],
			// Loading
			CustomBar: this.CustomBar,
			loadingTime: 0,
			// 防止重复点击
			buttonClicked: false,
		}
	},
	onLoad() {
	},
	methods: {
		// Loading
		loadProgress() {
			this.loadingTime += 3;
			if (this.loadingTime < 100 || this.isLoading) {
				setTimeout(() => {
					this.loadProgress();
				}, 100)
			} else {
				this.loadingTime = 0;
			}
		},
		// 上传视频相关
		selectVideo() {
			this.loadProgress()
			this.isLoading = true
			// this.LoadProgress()
			uni.chooseVideo({
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.isLoading = false
					this.duration = res.duration
					// 微信手机端不存在 thumbTempFilePath 小坑！
					// this.tempCoverUrl = res.thumbTempFilePath
					this.tempHeight = res.height
					this.tempWidth = res.width
					this.tempVideoUrl = res.tempFilePath
					if (res.duration > 21) {
						uni.showToast({
							title: '视频长度不能超过20秒',
							icon: 'none',
							duration: 2000
						})
						return
					} else if (res.duration < 4) {
						uni.showToast({
							title: '视频长度太短，请上传超过3秒的视频',
							icon: 'none',
							duration: 2000
						})
						return
					} else {
						this.isSelectVideo = true
						uni.showToast({
							title: '视频读取成功！',
							icon: 'none',
							duration: 1000
						})
					}

				},
				fail: () => {
					this.isLoading = false
					uni.showToast({
						title: '视频读取失败！',
						icon: 'none',
						duration: 1000
					})
				}
			})
		},
		toSetCoverImage() {
			console.log(this.isSetCoverImage);
			if (this.isSetCoverImage === false) {
				this.tempCoverUrl = ''
			}
			this.isSetCoverImage = !this.isSetCoverImage
		},
		setCoverImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				success: (res) => {
					this.tempCoverUrl = res.tempFilePaths[0]
				}
			})
		},
		handleNext() {
			if (this.basics == 0 && this.isSelectVideo) {
				this.basics++
				uni.request({
					url: this.baseUrl + '/bgm/list',
					method: 'POST',
					success: (res) => {
						if (res.data.status === 200) {
							this.bgmList = res.data.data
						}
					}
				})
			} else if (this.basics == 1) {
				// 防止重复提交
				if (this.buttonClicked) {
					uni.showToast({
						title: '正在提交中...',
						icon: 'none',
						duration: 1000
					})
					return
				}
				// 上传作品操作
				this.loadProgress()
				this.isLoading = true
				this.isUpload = false
				this.buttonClicked = true
				// this.LoadProgress()
				let user = getApp().globalData.getGlobalUserInfo()
				uni.uploadFile({
					url: this.baseUrl + '/video/upload',
					formData: {
						userId: user.id,
						bgmId: this.bgmId,
						description: this.description,
						duration: this.duration,
						videoHeight: this.tempHeight,
						videoWidth: this.tempWidth
					},
					filePath: this.tempVideoUrl,
					name: 'file',
					success: (res) => {
						let data = JSON.parse(res.data)
						if (data.status === 200) {
							// 上传封面图片
							if (this.tempCoverUrl != '') {
								let videoId = data.data
								uni.uploadFile({
									url: this.baseUrl + '/video/uploadCover',
									formData: {
										userId: user.id,
										videoId: videoId,
									},
									filePath: this.tempCoverUrl,
									name: 'file',
									success: (res) => {
										let data = JSON.parse(res.data)
										this.isLoading = false
										if (data.status === 200) {
											this.basics++
											this.isUpload = true
											this.buttonClicked = false
										} else {
											uni.showToast({
												title: '上传失败(。-`ω´-)',
												icon: 'none',
												duration:2000
											})
											this.buttonClicked = false
										}
									}
								})
							} else {
								this.basics++
								this.isLoading = false
								this.isUpload = true
								this.buttonClicked = false
							}

						} else {
							uni.showToast({
								title: '上传失败(。-`ω´-)',
								icon: 'none'
							})
							this.isLoading = false
							this.buttonClicked = false
						}
					},
					fail: (err) => {
						this.isLoading = false
						this.buttonClicked = false
					}
				})
			}
		},
		openSelectModel() {
			if (!this.buttonClicked) {
				this.isShowSelectModel = true
			}
		},
		hideSelectModel() {
			this.isShowSelectModel = false
		},
		handleBack() {
			uni.navigateBack()
		},
		// 选择BGM
		checkBgm(id) {
			if (id != undefined) {
				this.bgmId = id
				this.bgmList.forEach((bgm) => {
					if (bgm.id === id) {
						this.bgmName = bgm.name
						this.bgmAuthor = bgm.author
						this.bgmSrc = this.baseUrl + bgm.path
						this.isSelectBgm = true
					}
				})
			} else {
				this.bgmId = ''
				this.bgmName = ''
				this.bgmAuthor = ''
				this.bgmSrc = ''
				this.isSelectBgm = false
			}
			this.hideSelectModel()
		},
		changeStep(index) {
			if (index < this.basics && this.basics != 2) {
				this.basics = index
			}
		}
	},
}
